import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../components/Home.vue"
import Diary from "../components/Diary.vue"
import About from "../components/About.vue"
import A from "../components/List/a.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/Diary',
    name: 'Diary',
    component: Diary
  },
  {
    path: '/About',
    name: 'About',
    component: About
  },
  {
    path: '/A',
    name: 'A',
    component: A
  },

]

const router = new VueRouter({
  routes
})

export default router
